/*-------- Global Styles --------*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
}

/*-------- Head Section with Animation --------*/
.head {
  padding: 15px 0;
  color: #fff;
  animation: fadeInDown 1s ease-in-out;
  text-align: center;
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/*-------- Logo Animation --------*/
.logo {
  display: flex;
  gap: 15px;
  align-items: center;
  justify-content: center;
}

.logo img {
  width: 50px;
  border-radius: 50%;
  animation: pulse 2s infinite alternate;
}

@keyframes pulse {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.1);
  }
}

.logo h1 {
  font-size: 24px;
  font-weight: 900;
  animation: slideInLeft 1s ease-in-out;
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/*-------- Social Icons with Hover Animation --------*/
.social {
  display: flex;
  gap: 10px;
  justify-content: center;
}

.social i {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  font-size: 18px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.2);
  color: #fff;
  transition: transform 0.3s ease-in-out, background-color 0.3s ease-in-out;
}

.social i:hover {
  transform: scale(1.2);
  background-color: #1eb2a6;
}

/*-------- Responsive Header --------*/
header {
  background-color: rgba(255, 255, 255, 0.2);
  padding: 10px;
  animation: fadeIn 1s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

header ul {
  list-style: none;
  padding: 10px;
  text-align: center;
}

header ul li {
  display: inline-block;
  margin: 10px;
}

header ul li a {
  color: #fff;
  font-weight: 500;
  transition: 0.5s;
}

header ul li a:hover {
  color: #1eb2a6;
}

/* Start Button Animation */
.start {
  background-color: #1eb2a6;
  padding: 12px 30px;
  color: #fff;
  border-radius: 5px;
  transition: background-color 0.3s ease-in-out;
  animation: bounce 2s infinite;
  text-align: center;
  display: inline-block;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
}

.start:hover {
  background-color: #16a085;
}

/* Toggle Button */
.toggle {
  display: block;
  background: none;
  color: #fff;
  font-size: 24px;
  position: absolute;
  right: 20px;
  top: 10px;
}

.mobile-nav {
  display: none;
  position: absolute;
  top: 60px;
  left: 0;
  width: 100%;
  background-color: #1eb2a6;
  text-align: center;
  padding: 10px 0;
}

.mobile-nav ul {
  list-style: none;
  padding: 0;
}

.mobile-nav ul li {
  margin: 10px 0;
}

.mobile-nav ul li a {
  color: #fff;
  font-size: 18px;
}

/* Responsive Styles */
@media screen and (max-width: 768px) {
  .logo img {
    width: 40px;
  }
  .logo h1 {
    font-size: 20px;
  }
  .social i {
    width: 35px;
    height: 35px;
    font-size: 16px;
  }
  .start {
    padding: 10px 25px;
  }
  .toggle {
    font-size: 22px;
  }
}